.sort-block {
  width: 100%;
  padding: 15px 10px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sort-block__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.sort-block__item-label {
  line-height: 1;
  margin-right: 8px;
}
.sort-block__item-icon {
  width: 10px;
  height: 17px;
  margin-right: 8px;
  display: inline-block;
  background-image: url('@{imgPath}svg/lightning-icon.svg');
  background-repeat: no-repeat;
  background-size: 10px 17px;
}

.sort-block__modal-cont {
  .select2-search--dropdown {
    display: none !important;
  }
}