.other-articles {
  margin-top: 30px;
  margin-bottom: 30px;
  h4 {
    font-size: 16px;
    font-weight: normal;
    margin: 0;
    margin-bottom: 10px;
  }
}

.other-articles__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  li {
    display: inline-block;
    padding: 4px;
    padding-left: 8px;
    padding-right: 8px;
    background-color: #c5e8e9;
    margin-right: 4px;
    margin-bottom: 4px;
    a {
      line-height: 1;
      display: inline-block;
      border-bottom: 1px dashed #000;
    }
  }
}