.lib-main-links {
  padding-right: 80px;
  padding-bottom: 60px;
  h2 {  
    font-size: 20px;
    color: #40b3b7;
  }

  ul {
    padding: 0;
    list-style-type: none;
    font-size: 18px;
    margin: 0;
    li {
      margin-bottom: 20px;
      a {
        font-size: 18px;
        text-decoration: underline;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }

  .lib-main-links__all {
    text-align: right;
    a {
      color: #8a8a8a;
      text-decoration: underline;
      font-size: 18px;
      &:hover {
        text-decoration: none;
      }
    }
  }
}