.promo-link {
  margin-bottom: 24px;
  padding-left: 35px;
  position: relative;
  &::before {
    content: '%';
    color: #fff;
    background-color: #40b3b7;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
    font-weight: 500;
    line-height: 1;
  }
  a {
    font-size: 16px;
    color: #1bb2c0;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}