.sub-links {
  font-size: 14px;
}
.sub-links__item {
  margin-bottom: 20px;
} 
.sub-links__item-title {
  font-size: 14px;
  font-weight: normal;
  margin: 0;
  line-height: 1.5;
  display: inline-block;
  background-color: #c5e8e9;
  padding-left: 3px;
  padding-right: 3px;
  a {
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}
.sub-links__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  ul {
    padding: 0;
    list-style-type: none;
    margin: 0;
  }
  li {
    line-height: 24px;
    a {
      color: #1bb2c0;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
}
.sub-links__list-hidden {
  display: none;
}
.sub-links__more {
  margin-top: 5px;
  a {
    color: #777;
    text-decoration: underline;
    position: relative;
    &:hover {
      text-decoration: none;
    }
    &::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: 40%;
      margin-left: 5px;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 4px solid #777;
      border-bottom: none;
    }
  }
  &_less {
    a {
      &::after {
        border-top: none;
        border-bottom: 4px solid #777;
      }
    }
  }
}