.med-navigator {
  font-size: 16px;
  padding-bottom: 90px;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      padding-top: 8px;
      padding-bottom: 8px;
      a {
        font-size: 18px;
        text-decoration: underline;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}