.abc-nav {
  display: flex;
  background-color: rgb(64, 179, 183);
  padding: 20px;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 30px;
}

.abc-nav__pop {
  flex-grow: auto;
  padding-right: 30px;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}

.abc-nav__list {
  flex-grow: 1;
  padding: 0;
  list-style-type: none;
  margin: 0;
  display: flex;
  justify-content: space-between;
  li {
    
    a, span {
      font-size: 18px;
      text-transform: uppercase;
      font-weight: 700;
      color: #fff;
      line-height: 1;
    }
    span {
      opacity: 0.5;
    }
  }
}


