.company-card {
  padding: 30px;
  padding-bottom: 10px;
  border-radius: 4px;
  border: solid 1px rgba(51, 51, 51, 0.1);
  background-color: #ffffff;
  color: #333333;
  max-width: 960px;
  margin: auto;
  margin-bottom: 30px;
  font-size: 14px;
  h2 {
    font-size: 20px;
    line-height: 1.4;
    letter-spacing: normal;
    margin-top: 0px;
    margin-bottom: 8px;
  }
}
.company-card__head {
  padding-bottom: 20px;
  border-bottom: solid 1px rgba(51, 51, 51, 0.1);
}

.company-card__branch {
  padding-top: 20px;
  padding-bottom: 20px;
  &:not(:last-of-type) {
    border-bottom: solid 1px rgba(51, 51, 51, 0.1);
  }
}


.clinc-card__image {
  padding-left: 8px;
  padding-right: 8px;
  img {
    width: 100%;
  }
}


.company__card-rating {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.company-card__stars {
  position: relative;
  display: inline-block;
  height: 16px;
}
.company-card__grey-stars {
  width: 80px;
  height: 16px;
  display: inline-block;
  background-image: url('@{imgPath}svg/icn-rating--grey.svg');
}
.company-card__yellow-stars {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  height: 16px;
  background-image: url('@{imgPath}svg/icn-rating--yellow.svg');
}
.company-card__addr {
  margin-bottom: 10px;
}
.company-card__phone {
  a {
    color: #40b3b7;
    font-size: 12px;
  }
}
.company-card__stars {
  color: #ffa601;
  font-weight: bold;
  font-size: 20px;
  line-height: 1;
  margin-right: 8px;
}
.company-card__number-of-reviews {
  font-size: 12px;
  color: rgba(51, 51, 51, 0.75);
}


.company-card__addr {
  font-size: 14px;
  margin-bottom: 8px;
}
.company-card__spec {
  margin-bottom: 8px;
  span {
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 2px;
    background-color: rgba(64, 179, 183, 0.1);
  }
}
.company-card__graduation {
  margin-bottom: 8px;
  span {
    display: inline-block;
    padding-left: 25px;
    background-image: url('@{imgPath}svg/graduation-hat.svg');
    background-repeat: no-repeat;
    background-position: left top;
  }
}
.company-card__subway-station {
  font-size: 12px;
  padding-left: 20px;
  margin-left: 16px;
  background-image: url('@{imgPath}svg/metro-icon.svg');
  background-repeat: no-repeat;
  color: rgba(51, 51, 51, 0.75);
}

.company-card__description {
  margin-bottom: 30px;
  p {
    margin-bottom: 0;
    margin-top: 0;
  }
}
.company-card__calendar {
  
}
.company-card__more {
  color: #999999;
  display: inline-block;
  line-height: 1;
  &:hover {
    text-decoration: underline;
  }
  &_colored {
    color: #36979a;
    border-bottom: 1px dashed #36979a;
    &:hover {
      text-decoration: none;
    }
  }
  &_branches {
    margin-bottom: 15px;
    margin-top: 20px;
  }
}

.company-card__blockquote {
  margin: 0;
  padding-left: 30px;
  background-image: url('@{imgPath}svg/colored-quotes.svg');
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: 0 0;
}



.company-card__price-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  padding-left: 8px;
  padding-right: 8px;
  &:not(:last-child) {
    margin-bottom: 15px;
  }

  &_discount {
    padding-top: 15px;
    padding-bottom: 12px;
    border: 2px solid #ffa601;
    border-radius: 4px;
    position: relative;
    margin-bottom: 15px;
    &::before {
      content: 'Скидка НаПоправку';
      position: absolute;
      font-size: 12px;
      top: -10px;
      right: 10%;
      background-color: #fff;
      padding-left: 4px;
      padding-right: 8px;
      font-weight: 500;
      color: rgba(51, 51, 51, 0.75);
    }
    &::after {
      content: '25%';
      position: absolute;
      font-size: 12px;
      padding-top: 2px;
      line-height: 1;
      top: -8px;
      right: 10%;
      padding-left: 16px;
      padding-right: 16px;
      display: inline-block;
      transform: translateX(calc(100% - 8px));
      background-color: #fff;
      background-image: url('@{imgPath}svg/discount-table-bg.svg');
      background-repeat: no-repeat;
      background-position: center 0px;
      background-size: contain;
      font-weight: 500;
      color: rgba(51, 51, 51, 0.75);
    }
  }
}
.company-card__price-list-item {
  &:not(:last-of-type) {
    margin-bottom: 15px;
  }
  
  p {
    margin: 0;
  }
}
.company-card__price-list-price {
  font-weight: 500;
}
.company-card__price-list-cont {
  display: flex;
  justify-content: space-between;
}
.company-card__old-price {
  font-size: 12px;
  line-height: 1;
  position: relative;
  color: rgba(51, 51, 51, 0.5);
  &::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 1px;
    top: 40%;
    left: 0;
    background-color: rgba(51, 51, 51, 0.5);
  }
}
.company-card__additional-Info {
  padding-left: 10px;
  p {
    margin: 0;
    font-size: 12px;
    color: rgba(51, 51, 51, 0.75);
    margin-bottom: 5px;
    margin-top: 8px;
  }
} 
.company-card__additional-label {
  display: inline-block;
  font-size: 12px;
  border-radius: 3px;
  padding-left: 8px;
  padding-right: 8px;
  background-color: rgba(51, 51, 51, 0.05);
  span {
    font-weight: 500;
  }
}