.best-price-promo {
  margin-bottom: 40px;
  margin-top: 20px;
  h3 {
    font-size: 22px;
  }
}

.best-price-promo__cont {
  border: 1px solid #ddd;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}

.best-price-promo__item {
  width: 25%;
  padding: 25px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;


  .btn {
    display: block;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
    transition: box-shadow ease 200ms;
    &:hover {
      box-shadow: 0 -2px 0 #ffa601, 0 2px 0 #ffa601;
    }
  }
}

.best-price-promo__item-head {
  margin-top: 0;
  font-size: 20px;
 
  text-align: center;
  width: 100%;
  align-self: flex-start;
  min-height: 60px;
  a { 
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  
}

.best-price-promo__item-price {
  margin-bottom: 20px;
  text-align: center;
  width: 100%;
    span {
      font-weight: 700;
      display: inline-block;
      padding: 5px;
      line-height: 1;
      border: 2px solid rgb(255, 166, 1);
    }
}

.best-price-promo__common-link {
  text-align: right;
  margin-top: 8px;
  a {
    text-decoration: underline;
    color: #757575;
    &:hover {
      text-decoration: none;
    }
  }
}