.dropdown-head {
  display: inline-block;
  &:hover {
    h1 {
      a {
        &::after {
          transform: rotateZ(180deg);
        }
      }
    }
    .dropdown-head__dropdown {
      display: flex;
    }
  }
  h1 {
    margin-top: 15px;
    margin-bottom: 0;
    a {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        width: 14px;
        height: 6px;
        background-image: url('../media/images/svg/down-arr.svg');
        right: -30px;
        top: 10px;
        transition: all 100ms ease;
      }
    }
    
    span {
      display: inline-block;
      border-bottom: 2px dashed #000;
      line-height: 1;
    }
  }
}

.dropdown-head__cont {
  position: relative;
}
.dropdown-head__dropdown {
  position: absolute;
  width: 100%;
  border: 2px solid #40b3b7;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0;
  padding: 12px 30px;
  list-style-type: none;
  font-size: 18px;
  display: none;
  background-color: #fff;
  z-index: 11;
  margin: 0;
  margin-top: -2px;
  width: 100%;
  li {
    margin-top: 8px;
    margin-bottom: 8px;
    width: 50%;
    padding-right: 15px;
  }
  a {
    border-bottom: 1px solid #bfbfbf;
    &:hover {
      border-bottom: none;
    }
  }
}