.symptoms-anchors {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
}
.symptoms-anchors__item {
  padding: 20px;
  padding-bottom: 4px;
  border: 1px solid #40b3b7;
  width: calc(50% - 15px);
  h2 {
    margin-top: 0;
    color: #40b3b7;
    font-size: 30px;
  }
}
.symptoms-anchors__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  li {
    width: 50%;
    margin-bottom: 18px;
    font-size: 18px;
    line-height: 1;

    a {
      border-bottom: 1px dashed #bcbcbc;
      &:hover {
        border-bottom: none;
      }
    }
  }
}