.navigator {

}

.navigator-inner__specs {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}
.navigator-inner__specs-item {
  flex: 1 0 0;
  background-color: #40b3b7;
  padding: 10px;
  align-items: center;
  display: flex;
  &:not(:first-of-type) {
    margin-left: 20px;
  }
  h3 { 
    color: #fff;
    text-align: center;
    width: 100%;
    margin-top: 8px;
    margin-bottom: 8px;
  }
}



//////////////////////////////////////
.navigator__tabs {
  margin-bottom: 80px;

  ul {
    padding-left: 15px;
  }
}

.navigator__tabs-item {

}

.navigator__tabs-head {
  background-color: #e2e2e2;
  padding: 32px 20px;
  cursor: pointer;
  border-top: 1px solid #fff;
  position: relative;
  &.active {
    &::after {
      transform: rotateZ(180deg);
    }
  }
  &::after {
    content: '';
    width: 14px;
    height: 6px;
    position: absolute;
    display: block;
    top: 40px;
    right: 20px;
    background-image: url(../media/images/svg/down-arr.svg);
    transition: transform 300ms ease; 
  }
  p {
    margin: 0;
    font-size: 18px;
    line-height: 1.2;
  }
}

.navigator__tabs-dropdown {
  display: none;
}

.navigator__tabs-dropdown-cont {
  display: flex;
  justify-content: space-between;
}

.navigator__tabs-column {
  padding: 15px;
  padding-bottom: 80px;
  background-color: #f4f4f4;
  position: relative;
  flex: 1 0 0;
  &:not(:first-of-type) {
    margin-left: 20px;
  }
  h4, h2 {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 8px;
    &:first-of-type {
      margin-top: 0;
    }
  }
  p {
    margin-top: 8px;
    margin-bottom: 8px;
    &:first-of-type {
      margin-top: 0;
    }
  }
  .btn {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
  }
}

.navigator__inner-tab {
  background-color: #fafafb;
  margin-left: -15px;
  margin-right: -15px;
  border: 1px solid #e6e6e6;
  margin-bottom: 15px;
}
.navigator__inner-tab-head {
  padding-left: 20px;
  padding-right: 30px;
  padding-top: 12px;
  padding-bottom: 12px;
  position: relative;
  cursor: pointer;
  &.active {
    background-color: #e2e2e2;
    &::after {
      transform: rotateZ(180deg);
    }
  }
  h5 {
    margin: 0;
    font-size: 16px;
    font-weight: normal;
  }

  &::after {
    transform: rotateZ(0deg);
    transition: transform ease 300ms;
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    right: 11px;
    top: 50%;
    margin-top: -2px;
    border-top: 4px solid #444;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
  }
}

.navigator__inner-tab-dropdown {
  padding: 15px 20px;
  padding-bottom: 80px;
  position: relative;
  display: none;
  .btn {
    display: block;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    span {
      display: block;
      text-align: center;
    }
    small {
      display: block;
      line-height: 1;
      font-weight: normal;
      font-size: 12px;
      text-align: center;
    }
  }
}